<docs>
  # SwiperPhotoBio
  > A component that store an image slider 
  
  @author Kffein <contact@kffein.com>
</docs>

<script lang="ts">
import { VueInstance } from "@vueuse/core";
import SvgArrow from 'assets/svg/arrow.svg?raw';
import { store as themeStore } from "pages/Thematique/store";
import { store as appStore } from "store/app";
import { store as NavStore } from "ui/Header/store";
import { gsap } from "vendors/gsap";
import { defineComponent, PropType, reactive, ref, toRefs } from 'vue';
import { useRouter } from "vue-router";

export interface Data {
  options: Record<string, unknown>,
}

export default defineComponent({
  name: "ArticleObject",
  props: {
    images: {
      type: Array as PropType<{
        img: string,
        credit?: string
      }[]>,
      default: null
    }
  },
  setup(props) {
    const elPrev = ref<O<VueInstance>>()
    const elNext = ref<O<VueInstance>>()

    const router = useRouter();

    const data: Data = reactive({
      options: {
        slidesPerView: 2.8,
        spaceBetween: 10,
        loop: true,
        pagination: false,
        centeredSlides: false,
        overflow: false,
      },
    });

    const getAssetPath = (image: string): string => {
      return new URL(`../assets/${image}`, import.meta.url).href;
    };

    const goToCredit = (e : Event, credit:string) => {
      e?.preventDefault();

      themeStore.setCurrentObject(credit);

      gsap.timeline()
        .add( () => {
          NavStore.setHidden(true);
        }, 0)
        .add( () => {
          appStore.setAppBgColor("229, 240, 248");
        }, .5)
        .to(".page.bio", { opacity: 0, ease: "power4.out", duration: 1 }, 0)
        .add(() => {
          router.push({ name: 'credit', query: { section: credit }});
        }, 1)
    }

    return {
      ...toRefs(props),
      elPrev,
      elNext,
      ...toRefs(data), //data
      getAssetPath,
      goToCredit,

      //svg
      SvgArrow,
    };
  },
});
</script>
<template>
  <div v-if="images && images.length > 0"
       ref="el"
       class="slider-img-bio"
  >
    <nav class="navigation">
      <ui-button ref="elPrev"
                 class="btn -prev"
                 theme="icon"
                 title="Prev"
                 :icon="SvgArrow"
      />
      <ui-button ref="elNext"
                 class="btn -next"
                 theme="icon"
                 title="Next"
                 :icon="SvgArrow"
      />
    </nav>
    <ui-swiper class="slider-images"
               data-slug="swiper-images"
               :overflow="true"
               :options="options"
               :pagination="false"
               :navigation="{
                 nextEl: elNext && elNext.$el,
                 prevEl: elPrev && elPrev.$el
               }"
    >
      <div v-for="(img, indexImg) in images"
           :key="indexImg"
           class="single-img"
      >
        <ui-picture class="sect-img"
                    :src="getAssetPath(img.img)"
                    cover="none"
        />
        <ui-button v-if="img.credit"
                   class="cred swiper-no-swiping"
                   theme="link"
                   label="Crédits"
                   tag="a"
                   :to="{ name: 'credit', query: { section: img.credit } }"
                   @click="goToCredit($event, img.credit)"
        />
      </div>
    </ui-swiper>
  </div>
</template>

<style lang="stylus" scoped>

.slider-img-bio
    .navigation
      f(r, $justify:space-between)
      width 100%
      z-index 4
      color currentColor
      r padding-left, 10px
      r padding-right, 10px
      r margin-bottom, 40px
      :deep()
        .btn.-prev .icon svg
          transform rotate(-180deg)
    .slider-images
      r padding-left, 10px
      r padding-right, 10px
      :deep() .single-img
        .sect-img
          width 100%
          height auto
        width 100%
        height auto

      .cred
        r margin-top, 30px
        color $c-light_blue_udm
</style>
