<docs>
  # PageBio
  > The bio page
  
  @author Kffein <contact@kffein.com>
</docs>

<script lang="ts">
import { templateRef, tryOnUnmounted, useTemplateRefsList, VueInstance } from "@vueuse/core";
import SvgArrow from 'assets/svg/arrow.svg?raw';
import axios from 'axios';
import { store as scrollStore } from "components/SmoothScroll/store";
import KFF from "components/ui/KFF.vue";
import { each } from "lodash";
import { store as themeStore } from "pages/Thematique/store";
import emitter, { EVENTS } from "plugins/store/events";
import { store as appStore } from "store/app";
import { store as NavStore } from "ui/Header/store";
import usePageFlow from 'utils/flow/page';
import { gsap } from "vendors/gsap";
import { defineComponent, onBeforeMount, provide, ref } from 'vue';
import { useRouter } from "vue-router";
import BackgroundImage from './assets/background_bio.jpg';
import SwiperImages from './blocks/SwiperPhoto.vue';

export interface Data {
  options: Record<string, unknown>,
}


export default defineComponent({
  name: "PageBiography",
  components: {
    SwiperImages,
    KFF
  },
  setup() {
    const hero = ref<any>()
    const btnPrev = useTemplateRefsList<VueInstance>()
    const NextBtn = useTemplateRefsList<VueInstance>()
    const router = useRouter();

    const el = templateRef<HTMLElement>("el");
    const { readyToRender, getData, onInitUi } = usePageFlow<PageBiography>({
      load() {
        return axios.get(`/bio/data.json`)
          .then(({ data }) => {
            return {

              ...data
            }
          })
      },
    });

    onBeforeMount( () => {
      if(!appStore.introSeen && appStore.redirection) {
        router.push("/");
        return;
      }
    });

    onInitUi(() => {
      gsap.timeline()
        .add(() => {
          el.value.querySelector(".background-img")?.classList.remove("hidden");
        }, .2)
        .add(() => {
          el.value.querySelector(".header")?.classList.remove("hidden");
        }, 1.2)
        .add(() => {
          each(el.value.querySelectorAll(".section"), elSection => elSection?.classList.remove("hidden"));
          el.value.querySelector(".KFF")?.classList.remove("hidden");
        }, 1.2)
        .add( () => {
          if (themeStore.currentObject) {
            //
            const swiperExceptions = ["bio4", "bio5", "bio6", "bio7", "bio8", "bio9", "bio10"]
            let currentObjectId = swiperExceptions.indexOf(themeStore.currentObject) === -1 ? themeStore.currentObject : 'swiper-images';

            const selectedObject = el.value.querySelector(`[data-slug="${currentObjectId}"]`);
            themeStore.setCurrentObject();
            scrollStore.scrollTo(selectedObject as HTMLElement, { duration: 1.5 });
          }
        }, 1.25)

      emitter.on(EVENTS.PAGE_TRANSITION, onPageTransition);
    });

    const page = getData();
    provide("page", page);

    const getAssetPath = (image: string): string => {
      return new URL(`./assets/${image}`, import.meta.url).href;
    };

    const onPageTransition = (params: any) => {
      el.value.querySelector(".background-img")?.classList.add("hidden");
      el.value.querySelector(".header")?.classList.add("hidden");
      el.value.querySelector(".KFF")?.classList.add("hidden");

      each(el.value.querySelectorAll(".section"), elSection => elSection?.classList.add("hidden"));

      gsap.timeline()
        .add(() => {
          NavStore.setHidden(true);
        }, 0)
        .add(() => {
          if (params?.bgColor) appStore.setAppBgColor(params?.bgColor);
        }, .6)
        .add(() => {
          router.push({ path: params?.url });
        }, 1)

    }

    const goToCredit = (e : Event, credit:string) => {
      e?.preventDefault();

      themeStore.setCurrentObject(credit);

      gsap.timeline()
        .add( () => {
          NavStore.setHidden(true);
        }, 0)
        .add( () => {
          appStore.setAppBgColor("229, 240, 248");
        }, .5)
        .to(el.value, { opacity: 0, ease: "power4.out", duration: 1 }, 0)
        .add(() => {
          router.push({ name: 'credit', query: { section: credit }});
        }, 1)
    }

    tryOnUnmounted(() => {
      emitter.off(EVENTS.PAGE_TRANSITION, onPageTransition);
    })

    return {
      readyToRender,
      page,
      hero,
      BackgroundImage,
      el,
      btnPrev,
      NextBtn,
      SvgArrow,
      getAssetPath,
      goToCredit
    };
  },
});
</script>

<template>
  <main ref="el" class="page bio">
    <div class="background-img hidden">
      <ui-picture v-if="BackgroundImage"
                  class="back-img"
                  :src="BackgroundImage"
                  cover="cover"
                  :parallax="true"
                  :parallax-speed="0.5"
                  :parallax-direction="1"
                  :scrub="0.5"
      />
    </div>
    <header class="header hidden">
      <h1 class="title" v-html="page.title" />
    </header>

    <div v-for="(sec, index) in page.section"
         :key="index"
         class="section hidden"
    >
      <div class="left-section">
        <h2 class="sect-title" v-html="sec.title" />
      </div>
      <div class="right-section">
        <div class="index-title" v-html="(index + 1 < 10 ? '0' : '') + (index + 1)" />
        <div class="bottom-right">
          <div class="text-sect wysiwyg" v-html="sec.text" />
        </div>
      </div>
      <div v-if="sec.images || sec.video"
           :key="index"
           class="sect-bottom"
      >
        <SwiperImages v-if="sec.images && sec.images.length > 0"
                      :images="sec.images"
        />


        <div v-if="sec.video" class="video">
          <div v-for="(vid, indexVid) in sec.video"
               :key="indexVid"
               class="single-video"
          >
            <ui-video class="video-bio"
                      data-variant="university"
                      :poster="getAssetPath(vid.poster)"
                      :src="getAssetPath(vid.video)"
                      :data-slug="vid.credit"
            />
            <ui-button v-if="vid.credit"
                       class="cred"
                       theme="link"
                       label="Crédits"
                       tag="a"
                       :to="{ name: 'credit', query: { section: vid.credit } }"
                       @click="goToCredit($event, vid.credit)"
            />
          </div>
        </div>
      </div>
    </div>
    <KFF class="hidden" :invert="true" />
  </main>
</template>

